@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500&display=swap);
:root{--white: #ffffff;--white-x: #f2f2f2;--border: #c4bcbc;--black: #000000;--orange: #e0b99e;--light-gray: #a9a9a9}*,:after,:before{box-sizing:border-box}a{text-decoration:none;color:#ffffff;color:var(--white)}ul{list-style:none}html,body{height:100vh}body{font-family:'Poppins', sans-serif;font-size:1rem;font-weight:normal;margin:0px;background-color:#f2f2f2;background-color:var(--white-x);margin-bottom:2rem}ul,h1,h2,h3,h4,h5,p{margin:0;padding:0}h1{text-transform:capitalize;font-size:2rem}h2{font-size:1.4rem}h3{font-size:1.1rem}.img-fluid{max-width:100%;height:auto}img{vertical-align:middle;border-style:none}.truncate{width:200px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.container{position:relative;min-height:100vh}.loading{margin-top:1rem;display:flex;align-items:center;justify-content:center;font-size:1.5rem}.middle-wrapper{margin-top:1.5rem;padding-bottom:4.5rem;padding-left:1rem;padding-right:1rem}.middle-wrapper .search__post{width:250px;border-radius:5px;padding:.5rem .4rem}.middle-wrapper .show__text{font-size:.7rem;margin-top:.2rem;color:#a9a9a9;color:var(--light-gray)}header{background-color:#000000;background-color:var(--black);padding:1rem}.folder__wrapper .folder__list{display:grid;grid-template-columns:minmax(250px, 1fr) 1fr 1fr;grid-gap:1rem}@media screen and (max-width: 1024px){.folder__wrapper .folder__list{grid-template-columns:minmax(250px, 1fr) 1fr}}@media screen and (max-width: 568px){.folder__wrapper .folder__list{grid-template-columns:minmax(250px, 1fr)}}.folder__wrapper .folder__list li{margin-top:1.5rem}.folder__wrapper .folder__list li .folder__images{display:grid;grid-gap:0.2rem;grid-template-columns:auto auto;grid-template-rows:repeat(2, 12vw);border-radius:5px;background-color:#a9a9a9;background-color:var(--light-gray)}@media screen and (max-width: 1024px){.folder__wrapper .folder__list li .folder__images{grid-template-rows:repeat(2, 16vw)}}@media screen and (max-width: 568px){.folder__wrapper .folder__list li .folder__images{grid-template-rows:repeat(2, 25vw)}}.folder__wrapper .folder__list li .folder__images .img__0,.folder__wrapper .folder__list li .folder__images .img__1,.folder__wrapper .folder__list li .folder__images .img__2{outline:2px solid #ffffff}.folder__wrapper .folder__list li .folder__images .img__0{grid-column-start:1;grid-column-end:3;grid-row-start:1;grid-row-end:2}.folder__wrapper .folder__list li .folder__images .img__1{grid-column-start:1;grid-column-end:2;grid-row-start:2;grid-row-end:3}.folder__wrapper .folder__list li .folder__images .img__2{grid-column-start:2;grid-column-end:3;grid-row-start:2;grid-row-end:3}.folder__wrapper .folder__list li .folder__images .img__col img{width:100%;height:100%;object-fit:cover;text-indent:-9999px}.folder__wrapper .folder__list li h3{text-transform:uppercase;margin-top:0.8rem}.folder__wrapper .folder__list li p{color:#a9a9a9;color:var(--light-gray);margin-top:0.7rem}.folder__wrapper .folder__list li p span.orange{color:#e0b99e;color:var(--orange);margin:0px 4px}footer{display:flex;align-items:center;padding:1rem;position:absolute;bottom:0;width:100%;height:2.5rem;background-color:#000000;background-color:var(--black);color:#ffffff;color:var(--white)}



